.passports {
  width: 595px;
  margin: 0;
  &__button {
    padding: .8rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 4px;

    &--print {
      border: 2px solid darkslategray;
    }
    &--clear {
      background: #ff3434;
      color: #fff;
    }
  }
}

